import { useAuth0 } from "@auth0/auth0-react";
import { useContext, useEffect, useState } from "react"
import { userAuthContext, userGlobalInfoContext } from "../contextProviders/userContext";
import "../css/LoginModal.css"
import { createUserApiCall, loginApicall } from "./ApiCallHelpers";
import iconCheck from "../../Icons/check.png"




export function LoginButton() {
    
    const {loginWithRedirect} = useAuth0();

    return(
        <button className="loginButton font-500 font-16" onClick={loginWithRedirect}>Ingresa</button>
    )
}




export default function LoginModal(props) {
        //usestate para desplegar login o singin
        let [handleFormCreate, setHandleFormCreate] = useState(false);
            const changehandleformCreateState = ()=>{
                setHandleFormCreate(handleFormCreate? false : true)
                console.log(handleFormCreate)
            }

        //usestate para los valores por defecto de los inputs de iniciar sesión
        const [inputLoginDefaultValues, setInputLoginDefaultValues] = useState({
            email: "",
            password: "",
        })
    
    return(
        
     <div className="login-modal-outside-container">
           <div className="login-modal-main-container  ">
            <div className="login-modal-container  ">
                {!handleFormCreate? <LoginForm defaultValues={inputLoginDefaultValues} alertHandler={props.alertHandler}
                 handlerstateFormCreate={changehandleformCreateState}/> 
                 : <CreateForm handlerstateFormCreate={changehandleformCreateState}
                    alertHandler={props.alertHandler} setDefaultLoginValues={setInputLoginDefaultValues}
                 />}
            </div>
        </div>
     </div>
    )
}



function LoginForm(props) {


    //constexto global de usuario autenticado
    
    const [userAuthState, setUserAuthState ]= useContext(userAuthContext);

    //contexto global de la info del usuario
    const [userGlobalInfoState, setUserGlobalInfoState ]= useContext(userGlobalInfoContext);

 //llenamos un objeto con los valores de los inputs del login
    const [loginObj, setLoginObj] = useState({
        email: "",
        password: ""
    });
        function setLoginObjValues(e) {
            setLoginObj({
                ...loginObj,
                [e.target.name]: e.target.value
            })
            
        }

    
//submit del formulario de login
        function handleLoginSubmit(e) {
            e.preventDefault();
            
            //Validamos que los campos no estén vacíos
                if (loginObj.email.length === 0 || loginObj.password.length === 0) {
                    props.alertHandler(true,false,"Los campos están vacíos");
                    setTimeout(() => {
                        props.alertHandler(false,false)
                    }, 2000);
                }else{
                    //validamos que el email sea válido
                    const regExp = /^(([^<>()[\]\.,;:\s@\"]+(\.[^<>()[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i ;
                    
                        if (loginObj.email.match(regExp) === null) {    
                            props.alertHandler(true,false,"Email no válido");
                            setTimeout(() => {
                                props.alertHandler(false,false)
                            }, 2000);
                        }else{
                            if (loginObj.password.length > 5) {
                                //sacamos la ayuda visual de carga
                                props.alertHandler(true,true,"",true)
                                //enviamos la petición a ver si existe el usuario
                               


                                    //enviamos la petición con la funcion helper de llamada a la api

                                    loginApicall(loginObj)
                                        
                                        .then(resjson =>{
                                            
                                            if (resjson.error) {
                                                if (resjson.error === "Unprocessable Entity") {
                                                    
                                                    props.alertHandler(true,false,"verifica usuario y contraseña... o crea una cuenta");
                                                    setTimeout(() => {
                                                        props.alertHandler(false,false)
                                                    }, 2000);
                                                }
                                                if (resjson.error === `"email" must be a valid email`) {
                                                    props.alertHandler(true,false,"Email no válido");
                                                    setTimeout(() => {
                                                        props.alertHandler(false,false)
                                                    }, 2000);
                                                }
                                                
                                            }else{
                                                //una vez backend nos devuelve el logged in,
                                                // pasamos a cambiar el contexto global de authenticated
                                                setUserAuthState(true)
                                               
                                                props.alertHandler(true,true,"Sesión iniciada.");
                                                setTimeout(() => {
                                                    props.alertHandler(false,true)
                                                }, 2000);

                                                //tambien modificamos el contexto global de la info del usuario
                                                setUserGlobalInfoState({
                                                    picture: resjson.avatarUrl,
                                                    email: resjson.email,
                                                    name: resjson.name,
                                                    token: resjson.token
                                                })
                                             
                                            }
                                        })
                                        .catch(error=>{
                                            props.alertHandler(true,false,"Ocurrió un error desconocido, recargando...");
                                            setTimeout(() => {
                                                props.alertHandler(false,false);
                                                // window.location.reload();
                                            
                                            }, 2000);
                                        } )

                                
                            }else{
                                props.alertHandler(true,false,"La contraseña debe contener más de 6 caracteres");
                            setTimeout(() => {
                                props.alertHandler(false,false)
                            }, 2000);
                            }
                        }
                }

          
        }


    //useeffect para cuando se renderice, pasamos los valores por defecto de los inputs
    useEffect(() => {
        //si tenemos valor predefinidos para los inputs de logeo, los cambiamos también en el estado de este componente
        setLoginObj({
            ...loginObj,
            email: props.defaultValues.email,
            password: props.defaultValues.password
        })
    }, [props.defaultValues])
    

    return(
        <form className="shadow-1 flex-column justify-center align-center login-modal-form login-modal-form-2 color-h">
            <label className="flex-column">
                <p className="font-500">Email.</p>
                <input onChange={setLoginObjValues} className="mb-16"  type="email" name="email" defaultValue={props.defaultValues.email? props.defaultValues.email : ""} />
                <p className="font-500">Contraseña.</p>
                <input onChange={setLoginObjValues} className="mb-16" type="password" name="password"  defaultValue={props.defaultValues.password? props.defaultValues.password : ""} />
          
            </label>
            <input onClick={handleLoginSubmit} className="mt-16" type="submit" value="Iniciar sesión" />
            <p onClick={props.handlerstateFormCreate} className="align-self-end login-modal-create-account-p color-h">Crea una cuenta...</p>
        </form>
    )
}



function CreateForm(props) {

          
     // usestate del objeto contenedor de los inputs
     const [newAccountObj, setNewAccountObj] = useState(
         {
             avatarUrl: "",
             name: "",
             password: "",
             phone: "",
             email: ""
         }
     )





//     //usestate para manejar el color del boton de subir foto
     const [classPhotoUploadStyle, setClassPhotoUploadStyle] = useState(false)




// // // logica del widget para seleccionar foto
  
        function handlePhotoUpload(e) {
            e.preventDefault();
            const myWidget = window.cloudinary.createUploadWidget({
                             cloudName: 'nevado-trek', 
                             uploadPreset: 'taskApp'}, (error, result) => { 
                               if (!error && result && result.event === "success") { 
                                 console.log('Done! Here is the image info: ', result.info);
                                 //llenamos el objeto para enviar a la llamada con la api
                                  setNewAccountObj({
                                      ...newAccountObj,
                                      avatarUrl: result.info.url
                                  })
    
                                 //cambiamos el estilo del botón
                                  setClassPhotoUploadStyle(false);
                                      setTimeout(() => {
                                          setClassPhotoUploadStyle(true)
                                     }, 1000); 
                                console.log(result.info)
                               }
                             }
                           );

            myWidget.open();
        }

        
    //logica para manejar la llenada de los  inputs
    function handleInputChange(e) {
        setNewAccountObj({
            ...newAccountObj,
            [e.target.name]: e.target.value
        })
    }

    //usestate para la contraseña 2
    const [password2State, setPassword2State] = useState({
        password2: ""
    })

    //logica para manejar el llenado del input de la segunda contraseña
    function handlePassword2Input(e) {
        setPassword2State({
            ...password2State,
            [e.target.name]: e.target.value
        })
    }



    //logica para manejar el submit del formulario

    function handleFormCreateSubmit(e) {
        e.preventDefault();
        //primero validamos si los campos no están vacíos
        const valuesArray = Object.values(newAccountObj);
        const isNotEmpty = valuesArray.every(value => value.length > 0);
        if (isNotEmpty) {
            //verificamos si la entrada de numero de telefono son solo numeros
            if (newAccountObj.phone.match( /^[0-9]+$/) && newAccountObj.phone.length > 9) {
                

                //Verificamos si la entrada de email es correcta
                if (newAccountObj.email.match( /^\w+([.-_+]?\w+)*@\w+([.-]?\w+)*(\.\w{2,10})+$/ )) {
                    
                    //verificamos si la contraseña es mayor a 6 caracteres
                    if (newAccountObj.password.length > 5) {
                        //verificamos si las contraseñas coincíden
                        if (newAccountObj.password === password2State.password2) {

                            //verificamos que el nombre tenga más de 3 caracteres
                            if (newAccountObj.name.length > 3) {
                                              //despues de pasar todas las validaciones, enviamos los datos por medio de la api para registrarnos
                                    props.alertHandler(true, true, "", true)
                                    createUserApiCall(newAccountObj)
                                        .then(json => {
                                            if (json.error) {
                                                // si hay un error lo mostramos por pantalla
                                                if (json.message === "User already registered") {
                                                    props.alertHandler(true,false, "Usuario ya registrado, inicia sesión.");
                                                    setTimeout(() => {
                                                        props.alertHandler(false,false)
                                                    }, 2000);
                                                }else{
                                                    console.log(json)
                                                    props.alertHandler(true,false, "Error desconocido, intenta nuevamente.");
                                                    setTimeout(() => {
                                                        props.alertHandler(false,false)
                                                    }, 2000);
                                                }
                                                
                                            }else{
                                                //si la api nos devuelve un true, mostramos notificación de succes.
                                                    // y vamos al modal de login con los datos llenos
                                                    props.alertHandler(true,true, "Usuario creado con exito, inicia sesión.");
                                                    setTimeout(() => {
                                                        props.alertHandler(false,true)
                                                    }, 2000);

                                                    props.setDefaultLoginValues({
                                                        email: newAccountObj.email,
                                                        password: newAccountObj.password
                                                    })

                                                    // finalmente, cerramos el modal de crear
                                                    props.handlerstateFormCreate();
                                            }
                                        })
                                    
                            }else{
                                props.alertHandler(true,false, "El nombre debe contener más de 3 caracteres.");
                            setTimeout(() => {
                                props.alertHandler(false,false)
                            }, 2000);
                            }
                        }else{
                            props.alertHandler(true,false, "Las contraseñas no coincíden.");
                            setTimeout(() => {
                                props.alertHandler(false,false)
                            }, 2000);
                        }
                        


                    }else{
                        props.alertHandler(true,false, "La contraseña debe ser 6 o más caracteres.");
                        setTimeout(() => {
                            props.alertHandler(false,false)
                        }, 2000);
                    }

                }else{
                    props.alertHandler(true,false, "Email no válido.");
                    setTimeout(() => {
                        props.alertHandler(false,false)
                    }, 2000);
                }
            }else{
                props.alertHandler(true,false, "Número de telefono no válido, debe ser mayor a 9 caractéres.");
                setTimeout(() => {
                    props.alertHandler(false,false)
                }, 2000);
            }
        }else{
            props.alertHandler(true,false, "Datos vacíos, verifica...");
            setTimeout(() => {
                props.alertHandler(false,false)
            }, 2000);
        }
     
    }
        




   return(
        <form  className="shadow-1 flex-column justify-center align-center  create-user-login-form color-h">
            <label className="flex-column">
                <p className="font-500">Nombre.</p>
                <input onChange={handleInputChange} className=""  type="name" name="name" />
                <p className="font-500">Telefono.</p>
                <input onChange={handleInputChange} className=""  type="name" name="phone" />
               
                <p className="font-500">Email.</p>
                <input onChange={handleInputChange} className="email-input-2"  type="email" name="email" />
                <p className="font-500">Contraseña.</p>
                <input onChange={handleInputChange} className="" type="password" name="password" />
                <p className="font-500">Confirma contraseña.</p>
                <input  onChange={handlePassword2Input}  className="" type="password" name="password2" />
                
           
                {/* <p className="font-500">Sube Foto.</p> */}
                <div    id="upload-widget" 
                        // onClick={()=> myWidget.open()} 
                        onClick={handlePhotoUpload}
                        className={`self-center font-500 flex-row justify-center align-center cloudinary-button ${classPhotoUploadStyle? "cloudinary-button-uploaded" : "" }`}>
                        Sube Foto
                        {classPhotoUploadStyle? <img className="ml-6" src={iconCheck} alt="iconcheck" /> : ""}
                </div>

            </label>
            <button onClick={handleFormCreateSubmit}  className=" mt-16 self-center create-user-form-submit-button" type="submit" value="Crear cuenta">Crear cuenta</button>
            <p onClick={props.handlerstateFormCreate}  className="form-create-p align-self-end login-modal-create-account-p">Ya tengo una cuenta...</p>
        </form>
   )
}