import {useContext, useState} from "react"
import "../css/ModalEditNote.css"
import {editNoteApiCall, getuserNotesApicall} from "./ApiCallHelpers";
import { userGlobalInfoContext } from "../contextProviders/userContext";
import { BackwardButton } from "./Ui";

export default function ModalEditNote(props) { // logic for event stop propagation
    const preventPropagation = (e) => {
        e.stopPropagation()
    }
    
    //contexto global de la info del usuario
    const [userGlobalInfoState, setUserGlobalInfoState] = useContext(userGlobalInfoContext);

    // objeto contenedor de toda la información de la nueva nota en creación
    const [newNoteInfoObj, setNewNoteInfoObj] = useState(props.editNote)


    // logica para llenar el titulo de la nota


    // logica para obtener valores de los inputs
    function getInputValue(e) {
        setNewNoteInfoObj({
            ...newNoteInfoObj,
            [e.target.name]: e.target.value
        })


    }


    // funcion para manejar lo que hace el botón input para editar la nota y llamar la alerta
    function handleSubmitEditNote(e) {
        e.preventDefault();

        // validamos los formularios
        if (newNoteInfoObj.title.length === 0 || newNoteInfoObj.body.length === 0) {

            props.alertHandler(true, false, "Algunos espacios están vacíos.");
            setTimeout(() => {
                props.alertHandler(false, false)
            }, 2000);


        } else { // una vez validado el formulario, enviamos la alerta de cargando y hacemos la peticion
            props.alertHandler(true, true, "", true);

            // hacemos la petición asíncrona
            editNoteApiCall(newNoteInfoObj).then(resJson => {
                console.log(resJson)
                // si recibimos buena respuesta del server, hacemos...

                if (resJson.error) {
                    console.log("eerrrror al editar la nota--------", resJson.error)
                } else {
                    setTimeout(() => {
                        props.alertHandler(true, true, "Nota editada con éxito");
                        props.handleEditModal();
                        setTimeout(() => {
                            props.alertHandler(false, true)

                        }, 1000);
                    }, 2000);
                    //despues de obtener buena respuesta en la petición, cambiamos el estado global de las notas del usuario
                    console.log(userGlobalInfoState)
                    getuserNotesApicall(userGlobalInfoState)
                        .then(ResponseJson => {
                            //llenamos el contexto global con la información recibida de los
                            console.log(ResponseJson)
                            setUserGlobalInfoState({
                                ...userGlobalInfoState,
                                notes:  ResponseJson
                            })
                        })
                }
            }).catch((error) =>{
                //si encontramos un error en la petición
                setTimeout(() => {
                    props.alertHandler(true, false, "Hubo un error con la actualización de la Nota");
                    props.handleEditModal();
                    setTimeout(() => {
                        props.alertHandler(false, true)

                    }, 1000);
                }, 2000)
            } )

        }


    }


    return (
        <div onClick={
                props.handleEditModal
            }
            className="modal-edit-main-container flex-row justify-center align-center">
            <div className="modal-edit-container">
                <div onClick={preventPropagation}
                    className="flex-row justify-center note-edit-form-main-container">
                    <form className="note-edit-form-container shadow-1 flex-column justify-center align-center  color-h">
                        <label className="flex-column align-center note-edit-text-label-container ">
                            <p className="font-500">Título de la nota.</p>
                            <input onChange={getInputValue}
                                className="mb-16 modal-edit-title-input"
                                defaultValue={
                                    props.editNote.title
                                }
                                type="text"
                                name="title"/>
                            <p className="font-500">Cuerpo de la nota.</p>
                            
                            <textarea name="body"
                                defaultValue={
                                    props.editNote.body
                                }
                                onChange={getInputValue}>
                                {
                               
                            }</textarea>
                        </label>
                        <input onClick={handleSubmitEditNote}
                            className="mt--6 note-edit-submit-input"
                            type="submit"
                            value="Editar Nota"/>
                        
                       
                        <BackwardButton dinamicCloseComponentFunction={props.handleEditModal}/>
                    </form>


                </div>
            </div>

            
        </div>
    )
}
