import homeIcon from  "../../Icons/home.png"
import notebookIcon from  "../../Icons/notebook.png"
import plusIcon from "../../Icons/plus.png"
import settingsIcon from "../../Icons/settings.png"
import "../css/NavBar.css"
import "../css/GlobalStyles.css"
import { useEffect, useState } from "react"

export default function NavBar() {

    const [animationNavBar, setanimationNavBar] = useState(false);
    useEffect(() => {
        setTimeout(() => {
            setanimationNavBar(true)
        }, 500);
    
    }, [])
    
    return(
        <div className="flex-row justify-center nav-bar-main-container">
            <div className={` ${animationNavBar? "navBar-Container-down" : ""} navBar-container `}>
                
                <p className="font-20 color-a font-500">Guarda tus notas...</p>
            </div>

        </div>
    )
}