import { deleteNoteApiCall, getUserInfoFromApiCall, getuserNotesApicall } from "./ApiCallHelpers"
import editIcon from "../../Icons/edit.png"
import deleteIcon from "../../Icons/delete.png"
import "../css/ModalNoteList.css"
import React, { useContext, useEffect, useState } from 'react';
import ModalEditNote from "./ModalEditNote";
import { userGlobalInfoContext } from "../contextProviders/userContext";
import { BackwardButton } from "./Ui";





export default function ModalNoteList(props) {

    //logica para obtener la lista de las notas del contexto global de notas
    const [userGlobalInfoState, setUserGlobalInfoState] = useContext(userGlobalInfoContext);


    
    //logica para parar la propagación de evento
    function handleEventPropagation(e) {
        e.stopPropagation();
    }
   
    // logica para desplegar el formulario de edición de nota
    const [isOpenEditModal, setisOpenEditModal] = useState(false);


        const handleIsOpenEditModal = ()=>{
            setisOpenEditModal(isOpenEditModal? false : true)
        }
            let toEditNote;

    //hook del objeto que se le vapa a el formulario de edicion (nota)
    const [noteEditObj, setnoteEditObj] = useState({})


//logica para manejar el revertido del array de notas para mostrar de primero, el último que se creó


















    //logica para obtener la cantidad de tarjetas a crear
    const getCards = (e)=>{

        function openEditModal(note) {
            //espacio para que se vea la animación
            setTimeout(() => {
                      //llenar la variable para pasarla al modal de edición
            setnoteEditObj(note);
            

            //llamar el modal de edición
           
                handleIsOpenEditModal();
           
            }, 200);
           
        }

        //logica para eliminar la nota
        const handleDeleteNote = (noteInfo)=>{
            //mostramos la guía visual de carga
            props.alertHandler(true,true,"", true);
            deleteNoteApiCall(noteInfo)
                .then((resultJson) => {
                    // si se elimina la nota, procedemos a pasar alerta visual
                    if (!resultJson.error)   {

                        //recargamos el contexto global
                        //pero lo recargamos con el array revertido
                       
                        getuserNotesApicall(userGlobalInfoState)
                            .then((resultJson) => {
                                if(!resultJson.error){
                                    setUserGlobalInfoState({
                                        ...userGlobalInfoState,
                                        notes: resultJson
                                    })
                                }
                              
                            })
                            .catch((error) => {
                                props.alertHandler(true,false, "Ocurrió un error al actualizar las notas");
                                setTimeout(() => {
                                     props.alertHandler(false,false);
                                }, 1000); 
                              
                            })
                       //si no hay error, mostramos la alerta visual
                       props.alertHandler(true,true, "Nota eliminada satisfactoriamente");
                       setTimeout(() => {
                            props.alertHandler(false,true);
                       }, 1000); 


                    }else{
                        //si hay error, regresamos una nueva promesa en catch
                        props.alertHandler(true,false, "Ocurrió un error , intenta nuevamente");
                        setTimeout(() => {
                             props.alertHandler(false,false);
                        }, 1000); 
                    }
                  
                })
                .catch((error) => {
                  //si hay error mostramos alerta visual
                  props.alertHandler(true,false, "Ocurrió un error , intenta nuevamente");
                  setTimeout(() => {
                       props.alertHandler(false,false);
                  }, 1000); 
                })


        }

        //logica para obtener el array invertido para que se muestre la última nota que se creó, de primera

        let notasArray = [...userGlobalInfoState.notes].reverse();

        return( notasArray.map((note,i)=>{

            //logica para revertir 


            //logica para llamar el modal de edicion de nota y pasarle los valores de cada una de las notas
     
           
            
           

            let isCardPair;
            if (i%2 === 0 ) {
                isCardPair = true
            }else {
                isCardPair = false
            }
            
            return(<div key={note._id} onClick={handleEventPropagation} className={`flex-column align-center modal-note-card-main-container shadow-1 ${isCardPair? "modal-note-card-margin-top" : ""}`}>
                        <div className="shadow-1 color-f font-500 modal-note-card-title-container flex-row justify-center "><p>{note.title}</p></div>
                        <div className="color-h font-500 modal-note-card-body-container  "><p>{note.body}</p></div>
                        <div className="flex-row modal-note-icon-container">
                            <div onClick={()=> openEditModal(note)}  className=" modal-note-icon-container" > <img className="modal-note-card-edit-icon" src={editIcon}/> </div>
                            <div    onClick={()=> handleDeleteNote(note)} className=" modal-note-icon-container" > <img className="modal-note-card-edit-icon2" src={deleteIcon}/> </div>

                        </div>
                </div>)
        }))
    }


    
    
    //logica para manejar 

    // //logica para manejar el scroll de las notas
    // let swipeValue = 0;

    // function handlePageSwipe(e) {
    //     let parentDiv = e.target.parentElement;
    //         swipeValue = swipeValue - 50;
        

    //     parentDiv.style.transform = `translateY(${swipeValue}px)`
    //     console.log(swipeValue)
    // }

    return(
       <div>
            {isOpenEditModal? <ModalEditNote alertHandler={props.alertHandler} handleListModal={props.handleModal} handleEditModal={handleIsOpenEditModal} editNote={noteEditObj}/> : ""}
            <div  onClick={props.handleModal} className="modal-note-list-main-container" >    
                <div  className="modal-note-list-container flex-row">
                    <div className="modal-note-list-card-container  flex-row justify-center">
                        <div className="modal-note-list-fix-container font-20 ">{userGlobalInfoState.notes.length > 0? getCards() : "no hay notas, crea una..."}</div>
                    </div>

                    
                </div>
                
            </div>
           <div className="backwardButtonNoteListModal"> {isOpenEditModal? "" : <BackwardButton dinamicCloseComponentFunction={props.handleModal}/>}</div>
       </div>
    )
}