import { useEffect, useState } from "react";
import "../css/Header.css"

export default function Header() {

    const [animationNavBar, setanimationNavBar] = useState(false);
    
    useEffect(() => {
        setTimeout(() => {
            setanimationNavBar(true)
        }, 500);
    
    }, [])
    return(
        <div className="flex-row justify-center header-main-container">
            <div className={`shadow-1 header-h1-container flex-row justify-center align-center color-a ${animationNavBar? "header-h1-container-up" : ""}`}>
                <h1>  <span className="color-a">Tasky - App</span> </h1>

            </div>
        </div>
    )
}